$border-radius: 12px;

@media only screen and (max-width: 600px) {
  .button {
    flex-direction: column-reverse;
    #new-quote {
      margin-bottom: 10px;
    }
  }
}

.App {
  text-align: center;
}
#quote-box {
  padding: 20px;
  background-color: black;
  border-radius: 12px;
  color: black;
  #author {
    text-align: end;
  }
  .button {
    display: flex;
    justify-content: space-between;
    min-height: 50px;
    #tweet-quote {
      display: flex;
      float: left;
      padding: 10px;
      color: black;
      text-decoration: none;
      border-radius: 12px;
      align-self: center;
      justify-content: center;
  }
  }

  #new-quote {
    float: right;
    color: black;
    border-radius: $border-radius;
    padding: 10px;
    border: none;
    cursor: pointer;
    font-size: 16px;
  }
}
#name {
  font-size: 12px;

}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
